.customize-form-wrapper {
  align-items: flex-start;
  background-color: rgba(1, 1, 1, 0.8);
  display: flex;
  height: 100vh;
  justify-content: center;
  left: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1;
}

.customize-form {
  align-items: center;
  background-color: #022559;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  max-width: 600px;
  padding: 20px;
  position: relative;
  width: 70%;
  top: 100px;
}

.settings-wrapper {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.order-settings-wrapper,
.answer-settings-wrapper,
.level-settings-wrapper {
  display: flex;
  flex-direction: column;
  margin: 10px;;
}

.level-settings-wrapper label,
.order-settings-wrapper label {
  margin: 5px;
}

/* ------------ MOBILE --------------  */

@media (max-width: 500px) {
  .settings-wrapper {
    flex-direction: column;
    align-items: flex-start;
  }

  .customize-form {
    width: auto;
  }
}

